.pet {
  padding-top: 14px;
  @media screen and (max-width: 1220px) {
    padding-left: 30px;
    padding-right: 30px;
  }
  @media screen and (max-width: 920px) {
    padding-left: 40px;
    padding-right: 40px;
  }
  @media screen and (max-width: 740px) {
    padding-left: 20px;
    padding-right: 20px;
  }
  @media screen and (max-width: 610px) {
    padding-top: 12px;
    padding-left: 17px;
    padding-right: 17px;
  }   
}

.wrapper__pet {
  display: flex;
  position: relative;
  flex-direction: column;
  align-items: center;
}

.pet__buttons {
  padding-top: 80px;
  padding-bottom: 102px;
  @include transit;
  @media screen and (max-width: 1594px) {
    padding-bottom: 140px;
    @include transit;
  }
  @media screen and (max-width: 920px) {
    padding-top: 61px;
    padding-bottom: 100px;
  }
  @media screen and (max-width: 610px) {
    padding-top: 20px;
    padding-bottom: 60px;
  }  
}

.pet__flowers {
  width: 100vw;
  position: absolute;
  bottom: 0;
  display: flex;
  justify-content: space-between;
  align-items: end;
  font-size: 0;
  z-index: -1;
  .left_flower {
    overflow: hidden;
    img {
      width: 292px;
      height: 460px;
      object-fit: none;
      object-position: right top;
      @include transit;
    }
  }
  @media screen and (max-width: 1594px) {
    .left_flower {
      img {
        width: 292px;
        height: 300px;
        @include transit;
      }
    }
    .right_flower {
      img {
        width: auto;
        height: 260px;
        @include transit;
      }
    }
  }
  @media screen and (max-width: 1110px) {
    .left_flower {
      width: 192px;
      height: 202px;
      
      img {
        width: 260px;
        height:284px;
        object-fit: cover;
        transform: translateX(-66px);
        @include transit;
      }
    }
    .right_flower {
      img {
        width: auto;
        height: 180px;
        @include transit;
      }
    }
  }
  @media screen and (max-width: 920px) {
    .left_flower {
      img {
        transform: translateX(-126px);
      }
    }
    .right_flower {
      img {
        height: 130px;
      }
    }
  }
  @media screen and (max-width: 610px) {
    .left_flower {
      height: 72px;
      img {
        width: auto;
        height: 104px;
        transform: translateX(-84px);
      }
    }
    .right_flower {
      img {
        height: 70px;
        transform: translate(21px, 4px) ;

      }
    }
  } 
}

.carousel {
  width: 100%;
  position: relative;
  z-index: 0;
}

.carousel__hidder {
  width: 100%;
  position: relative;
  // width: 1160px;
  height: 872px;
  overflow: hidden;
  @media screen and (max-width: 1594px) {
    height: 944px;
  }
  @media screen and (max-width: 1220px) {
    height: 800px;
  }
  @media screen and (max-width: 980px) {
    height: 756px;
  }
  @media screen and (max-width: 610px) {
    height: 1388px;
  }

}

.carousel__container {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  // width: 1160px;
  height: 872px;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  row-gap: 20px;
  transition: row-gap 0.5s 0.3s, left 1s 0s;
  &_prev {
    left: -102%;
    transition: left 1s 0s;
  }
  &_next {
    left: 102%;
    transition: left 1s 0s;
  }
  @media screen and (max-width: 1594px) {
    height: 944px;
    row-gap: 92px;
    transition: row-gap 0.5s 0.2s, left 1s 0s;
  }
  @media screen and (max-width: 1220px) {
    height: 800px;
  }
  @media screen and (max-width: 980px) {
    height: 756px;
  }
  
  @media screen and (max-width: 920px) {
    .cart__border {
      &:nth-child(5) {
        display: none;
      }
      &:nth-child(6) {
        display: none;
      }
    }
  }
  @media screen and (max-width: 610px) {
    height: 1388px;
    flex-direction: column;
    align-items: center;
    row-gap: 20px;
    transition: row-gap 0s 0s, transform 1s 0s;
  } 
}

.carousel__container__buttons {
  position: absolute;
  width: calc(100% + 162px);
  left: -81px;
  top: calc(100% / 2);
  transform: translateY(-55%);
  display: flex;
  justify-content: space-between;
  z-index: -1;
  @include transit;
  .button {
    z-index: 1;
  }
  @media screen and (max-width: 1594px) {
    width: 100%;
    left: 0;
    padding-left: 323px;
    padding-right: 323px;
    z-index: 1;
    transition: all 0.5s 0.4s;
  }
  @media screen and (max-width: 980px)  {
    padding-left: 223px;
    padding-right: 223px;
  }
  @media screen and (max-width: 620px) {
    display: none;
  }
}
