.animalOnline {
  background-image: url("../../assets/images/GiantPanda.jpg");
  background-repeat: no-repeat;
  background-position: bottom;
  background-size: cover;
  overflow: hidden;

  @media screen and (max-width: 1200px) {
    background-size: 120% auto;

  }

}

.wrapper__animalOnline {
  display: flex;
  flex-direction: column;
  align-items: end;
  @media screen and (max-width: 1200px) {
    padding-right: 36px;
  } 
  @media screen and (max-width: 880px) {
    padding-right: 8px;
  }
  @media screen and (max-width: 400px) {
    padding-right: 10px;
  }
}
.animalOnline__button {
  padding-right: 100px;
  padding-bottom: 100px;
  @media screen and (max-width: 1200px) {
    padding-bottom: 47px;
  }
  @media screen and (max-width: 880px) {
    align-self: start;
    padding-left: 20px;
    padding-bottom: 31px;
    .button {
      transform: translateY(-9px);
    }
  }
  @media screen and (max-width: 580px) {
    .button {
      transform: translateY(0);
    }
  }
  @media screen and (max-width: 470px) {
    display: none;
  }
}
.watchBlock {
  margin-left: auto;
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 86px;
  width: 500px;
  font-weight: 700;
  font-size: 106.496px;
  line-height: 75%;
  color: #ffffff;
  text-align: center;
  text-transform: uppercase;
  z-index: 0;

  &::before {
    content: "";
    position: absolute;
    width: 465px;
    height: 465px;
    border-radius: 50%;
    background: linear-gradient(113.96deg, #f9804b 1.49%, #fe9013 101.44%);
    left: -74px;
    top: 11px;
    z-index: -1;

  }
  p {
    padding-bottom: 61px;
    span {
      color: #ffee2e;
    }
  }
  @media screen and (max-width: 1200px) {
    padding-top: 43px;
    font-size: 85px;

    &::before {
     width: 367px;
     height: 367px;
     left: -8px;
     top: -11px;

    }
    p {
      padding-bottom: 41px;

    }
  }
  @media screen and (max-width: 880px) {
    font-size: 55px;
    width: 272px;
    padding-top: 82px;
    &::before {
      width: 238px;
      height: 238px;
      left: -29px;
      top: 44px;
    }
    p {
      padding-bottom: 0;
    }
  }
  @media screen and (max-width: 580px) {
    font-size: 28px;
    width: 128px;
    padding-top: 75px;
    padding-bottom: 41px;
    &::before {
      width: 120px;
      height: 120px;
      left: -20px;
      top: 56px;
    }
  } 
}
